  methods: {
    getUserList() {
      this.$http.get('user', {params: this.queryInfo}).then(res => {
        this.userlist = res.data.data.records
        for (var i = 0; i < this.userlist.length; i++) {
          this.children[i] = [1,i,i+1]//动态向对象里添加数组
          this.$set(this.userlist[i], 'children', this.children[i])//向每一个userlist里添加children对象
        }
        for (var i = 0; i < this.userlist.length; i++) {
          this.children[i][0] = this.userlist[i].parentList[0]
          this.children[i][1] = this.userlist[i].parentList[1]
          for (var j =0 ;j<this.userlist[i].babyList.length;j++){
            this.children[i][j+2] = this.userlist[i].babyList[j]
            this.$set(this.children[i][j+2],'role',"宝宝")
          }
        }
        //总数据条数
        this.total = res.data.data.total
      }).catch(err => {
        this.$message.error('获取用户列表失败')
      })
    },
    //监听 size 改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.size = newSize
      this.getUserList()
    },
    //监听 页码值 改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.current = newPage
      this.getUserList()
    },
    //监听添加用户对话框的关闭事件
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    //点击按钮添加新用户
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        // console.log(valid)
        if (!valid) return
        //发起添加用户的网络请求
        const {data: res} = await this.$http.post('user', this.addForm)
        // console.log(res)
        if (res.status !== 200) {
          this.$message.error('添加用户失败')
        }
        this.$message.success('添加用户成功')
        //隐藏添加的用户对话框
        this.addDialogVisible = false
        //重新获取用户列表数据（刷新）
        this.getUserList()
        //清空添加用户对话框
        this.$refs.addFormRef.resetFields()
      })
    },
    //  添加宝宝
    //监听添加用户对话框的关闭事件
    addDialogClosedBaby () {
      this.$refs.addFormRefBaby.resetFields()
    },
    showAddBaby(id){
      this.uid = id
      this.addDialogVisibleBaby = true
    },
    //点击按钮添加新用户
    addBaby () {
      this.addFormBaby.age *= 1;
      this.addFormBaby.height *= 1.0;
      this.addFormBaby.weight *= 1.0;
      this.$refs.addFormRefBaby.validate(async valid => {
        if (!valid) return
        this.$set(this.addFormBaby,'userId',this.uid)
        //发起添加用户的网络请求
        const { data: res } = await this.$http.post('baby', this.addFormBaby)
        if (res.status !== 200) {
          this.$message.error('添加宝宝失败')
        }
        this.$message.success('添加宝宝成功')
        //隐藏添加的用户对话框
        this.addDialogVisibleBaby = false
        //重新获取用户列表数据（刷新）
        this.getUserList()
        // 清空添加用户对话框
        this.$refs.addFormRefBaby.resetFields()
      })
    },
    //  添加家长
    //监听添加用户对话框的关闭事件
    addDialogClosedParent () {
      this.$refs.addFormRefParent.resetFields()
    },
    showAddParent(id){
      this.uid = id
      this.addDialogVisibleParent = true
    },
    //点击按钮添加新用户
    addParent () {
      this.addFormParent.age *= 1;
      this.addFormParent.height *= 1.0;
      this.addFormParent.weight *= 1.0;
      this.$refs.addFormRefParent.validate(async valid => {
        if (!valid) return
        this.$set(this.addFormParent,'userId',this.uid)
        //发起添加用户的网络请求
        const { data: res } = await this.$http.post('parent', this.addFormParent)
        if (res.status !== 200) {
          this.$message.error('添加家长失败')
        }
        this.$message.success('添加家长成功')
        //隐藏添加的用户对话框
        this.addDialogVisibleParent = false
        //重新获取用户列表数据（刷新）
        this.getUserList()
        // 清空添加用户对话框
        this.$refs.addFormRefParent.resetFields()
      })
    },
    //展示编辑用户的对话框
    async showEditDialog(row) {
      if (row.role==='爸爸' || row.role==='妈妈'){
        this.editFormParent = row
        const {data: res} = await this.$http.put('parent', this.editFormParent)
        // console.log(res)
        if (res.status !== 200) {
          return this.$message.error('查询家长信息失败')
        }
        // console.log(this.editForm)
        this.editDialogVisibleParent = true
      }else if(row.role==='宝宝'){
        this.editFormBaby = row
        const {data: res} = await this.$http.put('baby', this.editFormBaby)
        // console.log(res)
        if (res.status !== 200) {
          return this.$message.error('查询宝宝信息失败')
        }
        // console.log(this.editForm)
        this.editDialogVisibleBaby = true
      }else {
        this.editForm.nickname = row.nickname
        this.editForm.username = row.username
        this.editForm.id = row.id
        const {data: res} = await this.$http.put('user', this.editForm)
        // console.log(res)
        if (res.status !== 200) {
          return this.$message.error('查询用户信息失败')
        }
        // console.log(this.editForm)
        this.editDialogVisible = true
      }
    },
    //监听修改用户对话框的关闭事件
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    editDialogClosedParent() {
      this.$refs.editFormRefParent.resetFields()
    },
    editDialogClosedBaby() {
      this.$refs.editFormRefBaby.resetFields()
    },
    //修改用户信息并提交
    editUserInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        //发起修改用户信息的数据请求
        const {data: res} = await this.$http.put('user', this.editForm)
        // console.log(res)
        if (res.status !== 200) {
          return this.$message.error('更新用户信息失败')
        }
        //关闭对话框
        this.editDialogVisible = false
        //刷新数据列表
        this.getUserList()
        //提示修改成功
        this.$message.success('更新用户信息成功')
      })
    },
    editParentInfo() {
      this.$refs.editFormRefParent.validate(async valid => {
        if (!valid) return
        //发起修改用户信息的数据请求
        const {data: res} = await this.$http.put('parent', this.editFormParent)
        // console.log(res)
        if (res.status !== 200) {
          return this.$message.error('更新家长信息失败')
        }
        //关闭对话框
        this.editDialogVisibleParent = false
        //刷新数据列表
        this.getUserList()
        //提示修改成功
        this.$message.success('更新家长信息成功')
      })
    },
    editBabyInfo() {
      this.$refs.editFormRefBaby.validate(async valid => {
        if (!valid) return
        //发起修改用户信息的数据请求
        const {data: res} = await this.$http.put('baby', this.editFormBaby)
        // console.log(res)
        if (res.status !== 200) {
          return this.$message.error('更新宝宝信息失败')
        }
        //关闭对话框
        this.editDialogVisibleBaby = false
        //刷新数据列表
        this.getUserList()
        //提示修改成功
        this.$message.success('更新宝宝信息成功')
      })
    },
    //根据id删除对应的用户信息
    async removeById(row) {
      if (row.role === '爸爸' || row.role === '妈妈'){
        //弹框询问用户是否删除数据
        const confirmResult = await this.$confirm('此操作将删除该家长, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
        //如果用户确认删除，则返回字符串 confirm
        //如果用户取消删除，则返回字符串 cancel
        // console.log(confirmResult)
        if (confirmResult !== 'confirm') {
          return this.$message.info('已取消删除')
        }
        this.$http.delete('parent?id=' + row.id).then(res => {
          this.$message.success('删除家长成功')
          this.getUserList()
        }).catch(err=>{
          console.log(err);
          return this.$message.error('删除家长失败')
        })

      }else if (row.role === '宝宝'){
        //弹框询问用户是否删除数据
        const confirmResult = await this.$confirm('此操作将删除该宝宝, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
        //如果用户确认删除，则返回字符串 confirm
        //如果用户取消删除，则返回字符串 cancel
        // console.log(confirmResult)
        if (confirmResult !== 'confirm') {
          return this.$message.info('已取消删除')
        }
        this.$http.delete('baby?id=' + row.id).then(res => {
          this.$message.success('删除宝宝成功')
          this.getUserList()
        }).catch(err=>{
          console.log(err);
          return this.$message.error('删除宝宝失败')
        })
      }else {
        //弹框询问用户是否删除数据
        const confirmResult = await this.$confirm('此操作将删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
        //如果用户确认删除，则返回字符串 confirm
        //如果用户取消删除，则返回字符串 cancel
        // console.log(confirmResult)
        if (confirmResult !== 'confirm') {
          return this.$message.info('已取消删除')
        }
        this.$http.delete('user?id=' + row.id).then(res => {
          this.$message.success('删除用户成功')
          this.getUserList()
        }).catch(err => {
          console.log(err);
          return this.$message.error('删除用户失败')
        })
      }
    },
    //获取角色对应名称
    getRoleName(row) {
      const role = row.role
      switch (role) {
        case 1:
          return '管理员'
        case 2:
          return '普通用户'
      }
    },
    //分配角色时角色对应名
    gRoleName(role) {
      switch (role) {
        case 1:
          return '管理员'
        case 2:
          return '普通用户'
      }
    },
    //展示分配角色的对话框
    async setRole(userInfo) {
      this.userInfo = userInfo
      const {data: res} = await this.$http.put('user', this.userInfo)

      if (res.status !== 200) {
        return this.$message.error('获取角色列表失败')
      }
      // console.log(this.userInfo)
      this.setRoleDialogVisible = true
    },
    //取消分配角色对话框
    exitRoleInfo() {
      this.setRoleDialogVisible = false
      this.seletedRoleId = ''
    },
    //分配角色信息并提交
    async savedRoleInfo() {
      //预验证
      if (!this.seletedRoleId) return this.$message.error('请选择要分配的角色')
      //将this.seletedRoleId的值传入
      this.userInfo.role = this.seletedRoleId
      //发起修改用户信息的数据请求
      const {data: res} = await this.$http.put('user', this.userInfo)
      if (res.status !== 200) {
        return this.$message.error('分配角色失败')
      }
      //关闭对话框
      this.setRoleDialogVisible = false
      //刷新数据列表
      this.getUserList()
      //提示修改成功
      this.$message.success('分配角色成功')

    }
  }
}
</script>

